const CrewCarousel = () => {
	const styles = useStyles();
	const isMobile = useMediaQuery(mobileSize);
	const isTablet = useMediaQuery(tabletSize);

	const slidesToShow = isMobile ? 1 : isTablet ? 2 : 4;

	const [width, setWidth] = useState(false);

	useEffect(() => {
		setWidth(isMobile);
	}, [isMobile]);

	return (
		<section className={styles.crewCarousel}>
			<Wrap>
				<Header1 title='Other project managers in our company' />
				<Carousel
					slidesToShow={slidesToShow}
					slidesToScroll={1}
					variableWidth={width}
					className={styles.customCarousel}>
					{teamList.map((item) => {
						return <CrewCard {...item} />;
					})}
				</Carousel>
			</Wrap>
		</section>
	);
};

export default CrewCarousel;
