import * as C from '../types';
import { get } from '../api';

export const getClientAll = () => async (dispatch, getState, api) => {
	dispatch({
		type: C.FETCH_CLIENT_ALL,
	});
	get('/client/getAll')
		.then(({ data }) => {
			dispatch({
				type: C.FINISHED_FETCH_CLIENT_ALL,
				data,
			});
		})
		.catch((error) => {
			dispatch({
				type: C.ERROR_FETCH_CLIENT_ALL,
				error,
			});
		});
};

