export const getClientProject =
	(projectId) => async (dispatch, getState, api) => {
		dispatch({
			type: C.FETCH_CLIENT_PROJECT,
		});
		get(`/client/get_${projectId}`)
			.then(({ data }) => {
				dispatch({
					type: C.FINISHED_FETCH_CLIENT_PROJECT,
					data,
				});
			})
			.catch((error) => {
				dispatch({
					type: C.ERROR_FETCH_CLIENT_PROJECT,
					error,
				});
			});
	};
