import React from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

import clsx from 'clsx';

import useStyles from './use-styles';

const NextArrow = ({ onClick }) => {
	const styles = useStyles();
	return (
		<button
			className={clsx(styles.slideArrow, styles.slideArrowNext)}
			onClick={onClick}>
			<img src={require('assets/icons/arrow-right.svg')} />
		</button>
	);
};

const PrevArrow = ({ onClick }) => {
	const styles = useStyles();
	return (
		<button
			className={clsx(styles.slideArrow, styles.slideArrowPrev)}
			onClick={onClick}>
			<img src={require('assets/icons/arrow-left.svg')} />
		</button>
	);
};
const Carousel = ({
	children,
	slidesToShow,
	slidesToScroll,
	variableWidth,
	dots,
	autoPlay,
	className,
}) => {
	const styles = useStyles();

	const settings = {
		infinite: true,
		speed: 500,
		autoplay: autoPlay,
		slidesToShow: slidesToShow,
		slidesToScroll: slidesToScroll,
		variableWidth: variableWidth,
		dots: dots,
		nextArrow: <NextArrow />,
		prevArrow: <PrevArrow />,
	};

	return (
		<div className={clsx(styles.slider, className)}>
			{<Slider {...settings}>{children}</Slider>}
		</div>
	);
};

export default Carousel;
