import React from 'react';
import Wrap from 'components/wrap';
import { Header1, Header4, Description1 } from 'components/typography';

import useStyles from './use-styles';

function Specification({ data }) {
	const styles = useStyles();
	return (
		<Wrap>
			<div className={styles.specification}>
				<Header1 title={data.heading} className={styles.customHead} />
				<div className={styles.infoWrap}>
					<Description1
						title={data.description}
						className={styles.topDescription}
					/>
					<div className={styles.workInfoWrap}>
						<Header4
							title={data.aditional.heading}
							className={styles.customHeading}
						/>
						{data.aditional.details.map((item) => {
							return (
								<div className={styles.workDescription}>
									<span className={styles.workHeading}>{item.naming}: </span>
									<span className={styles.workText}>
										{item.project.join(', ')}
									</span>
								</div>
							);
						})}
					</div>
				</div>
			</div>
		</Wrap>
	);
}

export default Specification;
